<template>

<div class="wrap">
  
  <div class="form-group">
    <div class="alert alert-success text-center"> {{ msg }} </div>
    <div class="row">
      <div class="col-12 text-center">
        <div class="row text-left">
          <div class="col-6">
            <label>Start date</label>
            <input type="date" class="form-control mb-2" v-model="dateStart">
          </div>
          <div class="col-6">
            <label>End date</label>
            <input type="date" class="form-control" v-model="dateEnd">
          </div>
        </div>
        <button class="btn btn-primary my-3"
          @click="queryBitcoin()">Fetch data</button>
      </div> 
    </div>

    <div class="row">
      <div class="col-12">
        <chartjs-line
          :labels="myLabels"
          :data="myData"
          :fill="true"
          :bind="true"></chartjs-line>
      </div>
    </div>
  </div> 
</div>

</template>

<script>
export default { 
  name: 'app',
  data () {
    return {
      msg: 'Bitcoin History',
      results: null,
      dateStart: '',
      dateEnd: '',
      toFill: true
    }
  },

  computed: {
    startEndsettings() {
      if(this.dateStart || this.dateEnd) {
        return `?start=${this.dateStart}&end=${this.dateEnd}`;
      }
      else {
        return '';
      }
    },
    myLabels() {
      if(this.results) {
        return Object.keys(this.results.bpi);
      }
      else {
        return [0];
      }
    },
    myData() {
      if(this.results) {
        return Object.values(this.results.bpi);
      }
      else {
        return [0];
      }
    }
  },

  methods: {
    queryBitcoin() {
      fetch("https://api.coindesk.com/v1/bpi/historical/close.json" + this.startEndsettings)
        .then((j) => {
          return j.json();
        })
        .then((r) => {
          this.results = r;
        })
    }
  }
}
</script>

<style lang="scss" scoped>
  
.wrap {
  max-width: 700px;
  margin: 20px auto;
  border-radius: 10px;
  box-shadow: 0 10px 70px rgba(0,0,0,0.1);
  padding: 20px;
}

.cst-label {
  font-size: 1.5em;
  text-align: center;
  display: block;
}

.fade-enter-active {
  animation: leftInX 0.5s linear;
}

.fade-leave-active {
  animation: leftOutX 0.5s linear; 
}

@keyframes leftInX {
  from{opacity:0;}
  to{opacity:1;}
}

@keyframes leftOutX {
  from{opacity:1;}
  to{opacity:0;}
}

</style>



